import React from 'react'
import 'lazysizes';

function IngImg({img, title, description}) {
  return (
    <div className='flex flex-col'>
    <div className='flex justify-center items-center bg-slate-100 size-48 rounded-xl'>
        <img className="lazyload size-36" data-src={img} alt=""/>
    </div>
    <div className='mt-2 gap-1 w-48 flex flex-wrap justify-center font-meduim text-xl'> 
        <p>{title}</p>
        <p className=' text-lg'>({description})</p>
    </div>
    </div>
  )
}

export default IngImg